<template>
  <div id="withdraw-audit" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-date-picker v-model="formInline.fromDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="formInline.toDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSearch()" icon="search"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="userlist">
      <el-table :data="tableData" stripe border style="width: 100%" :height="$store.state.tableHeight" type="selection" v-loading="loading">
        <el-table-column prop="createDate" label="" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spreadCode" label="" width="130"></el-table-column>
        <el-table-column prop="realName" label="" width="100"></el-table-column>
        <el-table-column prop="idcardNumber" label="" width="180"></el-table-column>
        <el-table-column prop="companyName" label="" ></el-table-column>
        <el-table-column  label="" >
           <template scope="scope">
              <i v-if="scope.row.ismerchant===1" class="el-icon-check"></i>
              <i v-if="scope.row.ismerchant===0" class="el-icon-minus"></i>            
            </template>
        </el-table-column>
        <el-table-column prop="ismarketing" label="">
           <template scope="scope">
              <i v-if="(scope.row.ismarketing===1)||(scope.row.isagent===1)" class="el-icon-check"></i>
              <i v-if="(scope.row.ismarketing===0)&&(scope.row.isagent!==1)" class="el-icon-minus"></i>            
            </template>
        </el-table-column>
        <el-table-column label="">
           <template scope="scope">
              <i v-if="scope.row.isagent===1" class="el-icon-check"></i>
              <i v-if="scope.row.isagent===0" class="el-icon-minus"></i>            
            </template>
        </el-table-column>
        <el-table-column prop="phoneNumber" label="" width="130"></el-table-column>
        <el-table-column prop="amount" label=""></el-table-column>
        <el-table-column prop="bankName" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="bankCard" label="" width="200"></el-table-column>
        <el-table-column label="" width="170">
          <template scope="scope">
            <el-button size="small" type="success" @click="handlePass(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleRefuse(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination @current-change="handleCurrentChange"
       :current-page="currentPage"
       :page-size="pageSize"
       layout="total, prev, pager, next"
       :total="total">
      </el-pagination>
    </div>
    <div class="withdrawDialog">
      <el-dialog title="" v-model="dialogFormVisible" size="tiny">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.memo" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="refuseConfirm"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        formInline: {
          fromDate: '',
          toDate: '',
          status: ''
        },
        form: {
          id: null,
          memo: ''
        },
        paginationState: false,
        tableData: [],
        currentPage: 1,
        pageSize: 1,
        total: 1,
        dialogFormVisible: false,
        formLabelWidth: '80px',
        tableDataIndex: null,
        loading: true,
        statusOptions: this.$store.state.withdrawStatus,
        value: '',
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick (picker) {
              picker.$emit('pick', new Date())
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
        }
      }
    },
    created: function () {
      this.getWithdraw()
    },
    methods: {
      getWithdraw (page = 1) {
        this.$axios.get(SITEURL.WITHDRAW_APPLY_URL + '?page=' + page).then(response => {
          this.loading = false
          this.tableData = response.data.data.data
          this.currentPage = response.data.data.current_page
          this.pageSize = response.data.data.per_page
          this.total = response.data.data.total
        }).catch(error => {
          console.log(error)
        })
      },
      onSearch (page = 1) {
        this.formInline.fromDate = typeof this.formInline.fromDate === 'undefined' ? '' : this.formInline.fromDate
        this.formInline.toDate = typeof this.formInline.toDate === 'undefined' ? '' : this.formInline.toDate
        this.loading = true
        if (this.formInline.fromDate === '' && this.formInline.toDate === '' && this.formInline.status === '') {
          this.getWithdraw()
        } else {
          this.$axios.get(SITEURL.WITHDRAW_SEARCH_URL + '?fromDate=' + this.formInline.fromDate + '&toDate=' + this.formInline.toDate + '&status=0&page=' + page)
            .then(response => {
              // get body data
              this.loading = false
              this.tableData = response.data.data.data
              this.currentPage = response.data.data.current_page
              this.pageSize = response.data.data.per_page
              this.total = response.data.data.total
            }).catch(error => {
              console.log(error)
            })
        }
        this.paginationState = true
      },
      handlePass (index, row) {
        this.$axios.post(SITEURL.WITHDRAW_AUDIT + '?id=' + row.id + '&status=4').then(response => {
          if (response.data.success) {
            this.$message.success('' + response.data.message)
            this.tableData.splice(index, 1)
          } else {
            this.$message.error('' + response.data.message)
          }
        }).catch(error => {
          console.log(error)
        })
      },
      handleRefuse (index, row) {
        this.form.id = row.id
        this.form.memo = row.memo
        this.tableDataIndex = index
        this.dialogFormVisible = true
      },
      refuseConfirm () {
        this.$axios.post(SITEURL.WITHDRAW_AUDIT + '?id=' + this.form.id + '&memo=' + this.form.memo + '&status=2').then(response => {
          if (response.data.success) {
            this.$message.success('' + response.data.message)
            this.tableData.splice(this.tableDataIndex, 1)
          } else {
            this.$message.error('' + response.data.message)
          }
          this.form.memo = ''
          this.form.id = null
          this.dialogFormVisible = false
        }).catch(error => {
          console.log(error)
        })
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.loading = true
        if (this.paginationState) {
          this.onSearch(val)
        } else {
          this.getWithdraw(val)
        }
      }
    }
  }
</script>

<style scoped>
  .el-form-item {
    margin-bottom: 0 !important;
  }
  .breadcrumb {
    margin: 20px 0 20px 0;
  }
  .search {
    padding: 20px;
    background: #d3dce6;
  }
  .userlist {
    padding-top: 20px;
  }
  .el-icon-check {
    color:#13ce66;
  }
  .el-icon-minus{
    color:#97a8be;
    font-size:16px;
  }
</style>



