<template>
  <div id="withdraw-audit" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-row>
          <el-col :span="18">
            <el-form-item label="">
              <el-date-picker v-model="formInline.fromDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker v-model="formInline.toDate" align="right" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSearch" icon="search"></el-button>
            </el-form-item>
          </el-col>
          <el-col :span="6" style="text-align: right;">
            <el-form-item>
              <el-button type="primary" @click="handleApply" icon="setting"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="userlist">
      <el-table :data="tableData" stripe border style="width: 100%" :height="$store.state.tableHeight" type="selection" v-loading="loading">
        <el-table-column prop="createDate" label="" width="180"></el-table-column>
        <el-table-column prop="modifyDate" label="" width="180"></el-table-column>
        <el-table-column prop="realName" label="" width="110"></el-table-column>
        <el-table-column prop="phoneNumber" label="" width="130"></el-table-column>
        <el-table-column prop="idcardNumber" label="" width="180"></el-table-column>
        <el-table-column prop="rechargeType" label=""></el-table-column>
        <el-table-column prop="rechargeExplain" label=""></el-table-column>
        <el-table-column prop="amount" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <template scope="scope">
            <el-tag :type="scope.row.status === '' ? 'success' : 'warning'" close-transition>{{scope.row.status}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="" :show-overflow-tooltip="true"></el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-size="pageSize"
                     layout="total, prev, pager, next"
                     :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        formInline: {
          fromDate: '',
          toDate: ''
        },
        form: {
          id: null,
          memo: ''
        },
        tableData: [],
        currentPage: 1,
        pageSize: 1,
        total: 1,
        formLabelWidth: '80px',
        tableDataIndex: null,
        loading: true,
        value: '',
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick (picker) {
              picker.$emit('pick', new Date())
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          }, {
            text: '',
            onClick (picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
        }
      }
    },
    created: function () {
      this.getRechargeList()
    },
    methods: {
      getRechargeList (page = 1) {
        this.$axios.get(SITEURL.OFFLINE_RECHARGE_LIST + '?page=' + page).then(response => {
          this.loading = false
          this.tableData = response.data.data.data
          this.currentPage = response.data.data.current_page
          this.pageSize = response.data.data.per_page
          this.total = response.data.data.total
        }).catch(error => {
          console.log(error)
        })
      },
      onSearch () {
        this.formInline.fromDate = typeof this.formInline.fromDate === 'undefined' ? '' : this.formInline.fromDate
        this.formInline.toDate = typeof this.formInline.toDate === 'undefined' ? '' : this.formInline.toDate
        this.loading = true
        if (this.formInline.fromDate === '' && this.formInline.toDate === '' && this.formInline.status === '') {
          this.getRechargeList()
        } else {
          this.$axios.get(SITEURL.OFFLINE_RECHARGE_SEARCH + '?fromDate=' + this.formInline.fromDate + '&toDate=' + this.formInline.toDate + '&type=list')
            .then(response => {
              // get body data
              this.loading = false
              this.tableData = response.data.data.data
              this.currentPage = response.data.data.current_page
              this.pageSize = response.data.data.per_page
              this.total = response.data.data.total
            }).catch(error => {
              console.log(error)
            })
        }
      },
      handleApply () {
        this.$router.push({ path: '/finance/recharge/apply' })
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.loading = true
        this.getRechargeList(val)
      }
    }
  }
</script>

<style scoped>
  .el-form-item {
    margin-bottom: 0 !important;
  }
  .breadcrumb {
    margin: 20px 0 20px 0;
  }
  .search {
    padding: 20px;
    background: #d3dce6;
  }
  .userlist {
    padding-top: 20px;
  }
</style>



